
import { Component, Vue } from "vue-property-decorator";
import DishDataService from "@/services/DishDataService";
import Dish from "@/types/Dish";
@Component
export default class TutorialsList extends Vue {
  private tutorials: Tutorial[] = [];
  private currentTutorial = {} as Tutorial;
  private currentIndex: number = -1;
  private title: string = "";
  retrieveTutorials() {
    TutorialDataService.getAll()
      .then((response) => {
        this.tutorials = response.data;
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }
  refreshList() {
    this.retrieveTutorials();
    this.currentTutorial = {} as Tutorial;
    this.currentIndex = -1;
  }
  setActiveTutorial(tutorial: Tutorial, index: number) {
    this.currentTutorial = tutorial;
    this.currentIndex = index;
  }
  removeAllTutorials() {
    TutorialDataService.deleteAll()
      .then((response) => {
        console.log(response.data);
        this.refreshList();
      })
      .catch((e) => {
        console.log(e);
      });
  }
  searchTitle() {
    TutorialDataService.findByTitle(this.title)
      .then((response) => {
        this.tutorials = response.data;
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }
  mounted() {
    this.retrieveTutorials();
  }
}
